iew-box .box-body{box-sizing:border-box;border:1px solid #151a30;border-top:none;background:#1a2138;border-bottom-left-radius:7px;border-bottom-right-radius:7px}@media (min-width: 700px){.box .box-body,.editor-box .box-body,.preview-box .box-body{position:absolute;top:2.5rem;bottom:0;width:100%}}.editor-box .box-head,.preview-box .box-head,.preview-box .box-body{font-family:"Roboto", sans-serif}#editor{font-family:"Roboto Mono", monospace}@media (min-width: 700px){.editor-box{-webkit-align-self:stretch;align-self:stretch;height:inherit}}@media (min-width: 700px){.editor-box .box-body{height:inherit}}.editor-box .box-body #editor{box-sizing:border-box;padding:0.75rem 1rem;background-color:#1a2138;border:none;outline:none;color:white;width:100%;min-height:20vh}@media (min-width: 700px){.editor-box .box-body #editor{height:100%}}@media (min-width: 700px){.preview-box{grid-column:2/3;grid-row:1/3;align-self:stretch;height:inherit}}.preview-box .box-body{padding:0.5rem 1rem;min-height:20vh}@media (min-width: 700px){.preview-box .box-body{max-height:inherit;overflow-y:scroll}.preview-box .box-body #preview{max-height:inherit}}#preview h1{font-weight:700}#preview h1:after{display:block;content:"";width:100%;height:2px;background:white;position:relative;top:3px}#preview h2{font-weight:500}#preview h2:after{display:block;content:"";width:100%;height:1px;background:white;position:relative;top:3px}#preview h3{font-weight:400}#preview h5{font-weight:400}#preview a{color:#98c910}#preview a:hover{color:#bade44}#preview a:visited{color:#7dac0b}#preview code{background-color:#222b45;border-radius:2px;color:#bade44}#preview pre{background-color:#222b45;padding:0.6rem 0.4rem;border-radius:5px}#preview blockquote p{position:relative;font-weight:600}#preview blockquote p:after{content:"";width:3px;height:100%;background:#98c910;position:absolute;top:0;left:-1rem;border-radius:3px}#preview table,#preview th,#preview td{border:solid 1px #2e3857}#preview table{border-collapse:collapse;wid